.library-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #F2F2F2;
    height: 100%;
    width: 100%;
}

.library-box {
    display: flex;
    justify-content: left;
    align-items: center;
    height: 80%;
    width: 80%; 
}

.goals-section-hide {
    height: 100%;
    width: 0%;
    border: 1px solid #C4C4C4;
    border-radius: 0.8rem;
    margin-right: 2%;
    padding-bottom: 1rem;
    animation: hide-goals 0.7s ease-in-out 1;
    background-color: white;
}

@keyframes hide-goals {
    0% {
        width: 30%;
    }
    100% {
        width: 0%;
    }
}


.goals-section {
    width: 30%;
    border: 1px solid #C4C4C4;
    background-color: white;
    border-radius: 0.8rem;
    margin-right: 2%;
    animation: show-goals 0.7s ease-in-out 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

@keyframes show-goals {
    0% {
        width: 0%;
    }
    100% {
        width: 30%;
    }
}

.library-section-expand {
    height: 100%;
    width: 100%;
    background-color: white;
    border: 1px solid #C4C4C4;
    border-radius: 0.8rem;
    padding-bottom: 1rem;
    animation: library-expand 0.7s ease-in-out 1;
}

@keyframes library-expand {
    0% {
        width: 68%;
    }
    100% {
        width: 100%
    }
}

.library-section {
    height: 100%;
    width: 68%;
    background-color: white;
    border: 1px solid #C4C4C4;
    border-radius: 0.8rem;
    padding-bottom: 1rem;
    animation: show-library 0.7s ease-in-out 1;
}

@keyframes show-library {
    0% {
        width: 100%;
    }
    100% {
        width: 68%;
    }
}

.library-banner2 {
    height: 60px;
    width: 100%;
    background-color: #78C6A3;
    border-radius: 0.8rem;
    border: 1px solid #06AB78;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.library-banner {
    height: 80px;
    width: 100%;
    background-color: #78C6A3;
    border-radius: 0.8rem;
    border: 1px solid #06AB78;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.adding-book-banner {
    position: absolute;
    top: 101%;
    width: 60%;
    height: 5.4rem;
    border-bottom-right-radius: 0.8rem;
    border-bottom-left-radius: 0.8rem;
    border: 1px solid #06AB78;
    border-top: none;
    background-color: #a1dac0;
    animation: banner 0.5s ease-in-out 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.pin-filled {
    animation: pin 0.5s ease-in-out 1;
}

@keyframes pin {
    0% {
        transform: scale(0.7);
    }
    85% {
        transform: scale(1.1);
    }
    100% {
        transform: scale(1);
    }
}

@keyframes banner {
    0% {
        height: 0rem;
    }
    100% {
        height: 5.4rem;
    }
}

.adding-book-banner-info {
